.gratitude-overlay{
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 8;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.8);
  cursor: pointer;
  display: none;
}

.overlay{
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 8;
  width: 100%;
  height: 100%;

  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 8;
    background-color: rgba(0,0,0,.7);
    cursor: pointer;
  }
  &__close{
    display: block;
    width: 30px;
    height: 30px;
    position: absolute;
    top: 10px;
    right: 30px;
    z-index: 8;
    @include mq($from:xs, $until:md){
      right: 20px;
    }

    &:before, &:after{
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      height: 100%;
      width: 4px;
      background-color: $black;
    }
    &:after{
      transform: translate(50%, -50%) rotate(45deg);
    }
    &:before{
      transform: translate(50%, -50%) rotate(-45deg);
    }
  }
  &__form{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 9;
    width: 100%;
    max-width: 570px;
    border-radius: 5px;
    background-color: $white;
    padding: 40px;
    @include mq($from:xs, $until:md){
      padding: 20px;
    }
  }
  .form{
    display: none;
  }
  .help-block {
    display: none;
  }
}